// src/App.jsx
import React, { useState, useEffect } from 'react';
import { HashRouter as Router, Routes, Route } from 'react-router-dom';
import './App.css';
import Layout from './components/Layout';
import NewsCard from './components/NewsCard';
import FilterPanel from './components/FilterPanel';
import ApiDebugTool from './components/ApiDebugTool';
import UrlCrawler from './components/UrlCrawler';
import AdminPanel from './components/AdminPanel';
import Login from './pages/Login';
import Register from './pages/Register';
import UserCenter from './pages/UserCenter';
import { fetchNews, fetchCategories } from './services/api';



function App() {
  const [news, setNews] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null); 
  const [category, setCategory] = useState('all');
  // 
  const [availableCategories, setAvailableCategories] = useState([]);
  // 
  const [hasMore, setHasMore] = useState(true);

  // 
  const [filters, setFilters] = useState({
    minScore: 0,
    days: 7,
    limit: 20, // 
    skip: 0    // 
  });
  
  // 
  const handleFilterChange = (newFilters) => {
    console.log(":", newFilters);
    //  skip  0
    setFilters({ ...newFilters, skip: 0 }); 
    setNews([]); // 
    setLoading(true); // 
  };

  // 
  const handleCategoryChange = (newCategory) => {
    setCategory(newCategory);
    //  skip  0
    setFilters(prev => ({ ...prev, skip: 0 })); 
    setNews([]); // 
    setLoading(true); // 
  }

  useEffect(() => {
    console.log(":", category, ":", filters);
    
    const loadNews = async () => {
      try {
        setError(null); // 
        setLoading(true); // 

        // 2.  fetchNews 
        const fetchedNews = await fetchNews({ 
          category: category === 'all' ? null : category, // 'all'  category 
          minScore: filters.minScore,
          days: filters.days,
          limit: filters.limit,
          skip: filters.skip
        });

        console.log(":", fetchedNews);
        
        //  (skip > 0)
        setNews(prevNews => filters.skip > 0 ? [...prevNews, ...fetchedNews] : fetchedNews);

        //  hasMore 
        setHasMore(fetchedNews.length === filters.limit);

      } catch (err) {
        console.error(":", err);
        setError(""); // 
      } finally {
        setLoading(false); // 
      }
    };

    loadNews();
  //  category  filters
  }, [category, filters]); 

  //  useEffect 
  useEffect(() => {
    const loadCategories = async () => {
      try {
        const fetchedCategories = await fetchCategories();
        //  null 
        setAvailableCategories(fetchedCategories.filter(cat => cat && cat.name));
      } catch (err) {
        console.error(":", err);
        // 
      }
    };
    loadCategories();
  }, []); // 

  // 
  const loadMore = () => {
    setFilters(prevFilters => ({
      ...prevFilters,
      skip: prevFilters.skip + prevFilters.limit
    }));
    //  setLoading(true) filters  useEffect
  };

  // MainContentRouterFilterPaneluseSearchParamsuseNavigate
  const MainContent = () => (
    <Layout>
        <FilterPanel 
           filters={filters} 
           onFilterChange={handleFilterChange} 
          />
          <div className="category-nav">
            {/*  handleCategoryChange  */}
            <button 
              className={`category-btn ${category === 'all' ? 'active' : ''}`}
              onClick={() => handleCategoryChange('all')}>
              
            </button>
            {/*  */}
         {availableCategories.map((cat) => (
           <button
             key={cat.id} //  ID  key
             className={`category-btn ${category === cat.name ? 'active' : ''}`}
             onClick={() => handleCategoryChange(cat.name)}>
             {cat.name}
           </button>
         ))}
       </div>
          
          {error && <div style={{ color: 'red', marginBottom: '1rem' }}>{error}</div>}

          <div className="news-container">
            {/*  */}
            {loading && news.length === 0 ? (
              <div className="loading-skeleton">
                {[...Array(5)].map((_, i) => (
                  <div key={i} className="skeleton-card">
                    <div className="skeleton-title"></div>
                    <div className="skeleton-meta"></div>
                  </div>
                ))}
              </div>
            ) : news.length > 0 ? (
              news.map((item) => (
                <NewsCard
                  key={item.id}
                  id={item.id}
                  title={item.title}
                  source={item.source}
                  //  published_at
                  published_at={item.published_at}
                  url={item.url}
                  //  summary  importance_score
                  summary={item.summary}
                  importance_score={item.importance_score}
                />
              ))
            ) : (
              // 
              !loading && news.length === 0 && <div></div>
            )}
            {/*  */}
            {!loading && news.length > 0 && hasMore && (
              <button 
                onClick={loadMore} 
                className="load-more-button" // 
                style={{ //  CSS 
                  display: 'block', 
                  width: '100%', 
                  padding: '0.8rem', 
                  marginTop: '1.5rem',
                  backgroundColor: 'var(--accent)',
                  color: 'white',
                  border: 'none',
                  borderRadius: '4px',
                  cursor: 'pointer',
                  fontSize: '0.95rem'
                }}
              >
                
              </button>
            )}
            {/*  */}
            {loading && filters.skip > 0 && (
              <div style={{textAlign: 'center', padding: '1rem', color: 'var(--text-secondary)'}}>
                ...
              </div>
            )}
          </div>
    </Layout>
  );
  
  return (
    <Router>
      <Routes>
        <Route path="/" element={<MainContent />} />
        <Route path="/login" element={<Login />} />
        <Route path="/register" element={<Register />} />
        <Route path="/user" element={<UserCenter />} />
        <Route path="/debug-api" element={<ApiDebugTool />} />
        <Route path="/crawler" element={<UrlCrawler />} />
        <Route path="/submit-url" element={<UrlCrawler />} />
        <Route path="/admin" element={<AdminPanel />} /> 
        {/*  */}
      </Routes>
    </Router>
  );
}

export default App;