
import React, { useState } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { registerUser } from '../services/api';
import { Form, Input, Button, Card, Typography, message } from 'antd';
import { UserOutlined, LockOutlined, MailOutlined } from '@ant-design/icons';

const { Title, Text } = Typography;

const Register = () => {
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();

  const onFinish = async (values) => {
    setLoading(true);
    try {
      await registerUser(values.username, values.email, values.password);
      message.success('');
      setTimeout(() => navigate('/login'), 1200);
    } catch (err) {
      message.error(err.message || '');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div style={{ minHeight: '100vh', display: 'flex', alignItems: 'center', justifyContent: 'center', background: '#f6f8fa' }}>
      <Card style={{ width: 400, borderRadius: 12, boxShadow: '0 2px 16px #0001' }}>
        <Title level={2} style={{ textAlign: 'center', marginBottom: 28 }}></Title>
        <Form name="register" onFinish={onFinish} layout="vertical" autoComplete="off">
          <Form.Item name="username" rules={[{ required: true, message: '' }]}> 
            <Input prefix={<UserOutlined />} placeholder="" size="large" />
          </Form.Item>
          <Form.Item name="email" rules={[{ required: true, message: '' }, { type: 'email', message: '' }]}> 
            <Input prefix={<MailOutlined />} placeholder="" size="large" />
          </Form.Item>
          <Form.Item name="password" rules={[{ required: true, message: '' }]}> 
            <Input.Password prefix={<LockOutlined />} placeholder="" size="large" />
          </Form.Item>
          <Form.Item>
            <Button type="primary" htmlType="submit" size="large" block loading={loading} style={{ borderRadius: 6 }}></Button>
          </Form.Item>
        </Form>
        <div style={{ textAlign: 'center', marginTop: 8 }}>
          <Text type="secondary"></Text>
          <Link to="/login" style={{ marginLeft: 4 }}></Link>
        </div>
      </Card>
    </div>
  );
};

export default Register;
