// frontend/src/components/UrlCrawler.jsx
import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import NewsDiscoveryButton from './NewsDiscoveryButton'; 
import './UrlCrawler.css';

const UrlCrawler = () => {
  const [url, setUrl] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [result, setResult] = useState(null);
  const [error, setError] = useState(null);
  const API_BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:8000/api';
  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!url) return;
    
    try {
      setIsLoading(true);
      setError(null);
      
      const response = await fetch(`${API_BASE_URL}/crawler/url`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ url }),
      });
      
      if (!response.ok) {
        throw new Error(`: ${response.status}`);
      }
      
      const data = await response.json();
      setResult(data);
      setUrl(''); // 
    } catch (err) {
      setError('URL: ' + err.message);
    } finally {
      setIsLoading(false);
    }
  };
  
  return (
    <div className="url-crawler">
      <h1 className="crawler-title"></h1>
      <p className="crawler-description">
        URLAI
      </p>
      
      <form onSubmit={handleSubmit} className="crawler-form">
        <div className="input-group">
          <input
            type="url"
            value={url}
            onChange={(e) => setUrl(e.target.value)}
            placeholder="URL (: https://www.bbc.com/news/world-asia-china-66186965)"
            required
            className="url-input"
          />
          <button 
            type="submit" 
            className="crawl-button"
            disabled={isLoading}
          >
            {isLoading ? '...' : ''}
          </button>
        </div>
        
        {error && (
          <div className="crawler-error">
            {error}
          </div>
        )}
        
        {isLoading && (
          <div className="crawler-loading">
            <div className="loading-spinner"></div>
            <p>...</p>
          </div>
        )}
        
        {result && !isLoading && (
          <div className="success-message" style={{
            padding: '1rem',
            backgroundColor: '#e8f5e9',
            borderRadius: '4px',
            marginTop: '1rem',
            textAlign: 'center'
          }}>
            <p>{result.message}</p>
            <p style={{fontSize: '0.9rem', marginTop: '0.5rem'}}>
              
            </p>
          </div>
        )}
      </form>

      <div className="discovery-section" style={{marginTop: '2rem', padding: '1rem', backgroundColor: '#f5f5f5', borderRadius: '4px'}}>
        <h3></h3>
        <p></p>
        <NewsDiscoveryButton />
      </div>
      
      <div className="ai-discovery-info" style={{marginTop: '2rem', padding: '1rem', backgroundColor: '#f8f9fa', borderRadius: '4px'}}>
        <h3>AI</h3>
        <p>URL</p>
        <ul style={{paddingLeft: '1.5rem'}}>
          <li></li>
          <li>30</li>
          <li>AI</li>
        </ul>
      </div>
      
      <div className="crawler-footer">
        <Link to="/" className="back-link"></Link>
      </div>
    </div>
  );
};

export default UrlCrawler;